.App {
  text-align: center;
}

li{
  list-style-type: none;
}

.exit{
  width: 300px;
  height: 60px;
	background-color: #4083b2;
	border: 5px solid white;
	border-radius: 20px;
	transition: 1s;
	display: block;
	margin: auto;
}

.exit:hover{
  background-color: #15ab74;
	border-radius: 50px;
  transition: 1s;
  cursor: pointer;
}

.exit1{
  background-color: #c8334d;
}

.next{
  width: 80px;
  height: 40px;
	background-color: pink;
	border: 5px solid white;
	border-radius: 20px;
	transition: 1s;
	display: block;
  margin: auto;
  outline-style: none;
}

.next:hover{
  background-color: #c8334d;
	border-radius: 50px;
  transition: 1s;
  cursor: pointer;
}

.back{
  width: 80px;
  height: 40px;
	background-color: #9e9e9e;
	border: 5px solid white;
	border-radius: 20px;
	transition: 1s;
	display: block;
  margin: auto;
  outline-style: none;
}

.back:hover{
  background-color: gray;
	border-radius: 50px;
  transition: 1s;
  cursor: pointer;
}

.click{
  cursor: pointer;
  width: 100px;
  height: 50px;
  background-color: pink;
  border: 5px solid pink;
  border-radius: 20px;
  outline-style: none;
}

